import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import Chip from 'material-ui/Chip';
import TextField from 'material-ui/TextField';

import Dialog, {
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from 'material-ui/Dialog';


const styleSheet = createStyleSheet('ChipsArray', theme => ({
  chip: {
    margin: theme.spacing.unit / 2,
  },
  row: {
    display: 'flex',
    justifyContent: 'left',
    flexWrap: 'wrap',
    padding: '20px 0px 20px 10px',
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap',
    padding: '0px 0px 20px 20px',
  },
  input: {
    fontSize: '0.8em',
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 180,
  },
}));

class ChipsArray extends Component {
  state = {
    chipData: [
    /*
    
      { key: 0, label: 'スタッフ番号' },
      { key: 1, label: '氏名' },
      { key: 2, label: '住所' },
      { key: 3, label: 'おすすめ' },
    */
    ],

    open: false,
  };

  /*
   * ダイアログ展開状態
   */
  handleRequestClose = () => {
    this.setState({ open: false });
  };

	/*
	 * テキストフィールド編集時のイベント
	 * 編集したテキストをステートにセットする
	 *
	 * index: textDataの編集対象の配列番号
	 * event: 編集したイベントデータ
	 *
	 */
  handleRequestChange = index => event => {
    const chipData = [...this.state.chipData];
    chipData.value = event.target.value;
    this.setState({ chipData: chipData });

    // コールバック関数が指定されていれば呼び出す
    if(this.props.onTextChange){
//      this.props.onTextChange(chipData);
    }
  };

	/*
	 * チップクリック時のイベント
	 * ダイアログを表示する
	 *
	 * data: クリック対象のイベントデータ
	 *
	 */
  handleRequestClick = data => () => {
    this.setState({ chipData: data });

    // ダイアログオープン
    this.setState({ open: true });
  };

	/*
	 * チップ削除時のイベント
	 *
	 * data: 削除したイベントデータ
	 *
	 */
  handleRequestDelete = data => () => {
    const chipData = this.props.chipData;
    const index = chipData.indexOf(data);
    chipData[index].value = '';
//    this.setState({ chipData });

    // コールバック関数が指定されていれば呼び出す
    if(this.props.onTextChange){
      this.props.onTextChange(chipData);
    }
  };

  render() {
    const classes = this.props.classes;

		{/* チップ追加 */}
    const chipData = this.props.chipData;

    return (
      <div className={classes.row}>
        {chipData.map(data => {
          if(data.value){
            {/* テキストが存在すれば表示する */}
	          return (
	            <Chip
	              label={data.value + '（' + data.label + '）'}
	              key={data.key}
                onClick={this.handleRequestClick(data)}
	              onRequestDelete={this.handleRequestDelete(data)}
	              className={classes.chip}
	            />
	          );
          }
        })}

				{/* ダイアログ */}
        <Dialog open={this.state.open} onRequestClose={this.handleRequestClose}>
          <DialogContent>
            <DialogContentText>
							検索結果：n 件
				      <div className={classes.container}>
				        <TextField
				          id="nowork"
				          label={this.state.chipData.label}
				          type="text"
				          className={classes.input}
				          value={this.state.chipData.value}
				          onChange={this.handleRequestChange(this.state.chipData.key)}
				          InputProps={{ placeholder: this.state.chipData.placeholder }}
				          marginForm
				        />
				      </div>

            </DialogContentText>
          </DialogContent>
        </Dialog>
      </div>
    );
  }
}

ChipsArray.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styleSheet)(ChipsArray);




