import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import TextField from 'material-ui/TextField';

const styleSheet = createStyleSheet('TextFields', theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
    padding: '0px 0px 20px 20px',
  },
  input: {
    fontSize: '0.8em',
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 180,
  },
}));

class TextFields extends Component {
  state = {
	  textData: [
	    { key: 0, label: 'スタッフ番号', value: '', placeholder: '例）000000000012' },
	    { key: 1, label: '氏名',         value: '', placeholder: '例）派遣　花子' },
	    { key: 2, label: '住所',         value: '', placeholder: '例）東京都台東区上野' },
	    { key: 3, label: 'おすすめ',     value: '', placeholder: '例）半年間働いていない' },
	  ],
  };

	/*
	 * テキストフィールド編集時のイベント
	 * 編集したテキストをステートにセットする
	 *
	 * index: textDataの編集対象の配列番号
	 * event: 編集したイベントデータ
	 *
	 */
  handleRequestChange = index => event => {
  /*
    alert('index = ' + index);
    alert('event.target.value = ' + event.target.value);
  */
    const textData = [...this.state.textData];
    textData[index].value = event.target.value;
    this.setState({ textData });

    // コールバック関数が指定されていれば呼び出す
    if(this.props.onTextChange){
      this.props.onTextChange(textData);
    }
  };

  render() {
    const textData = [...this.state.textData];
    const classes = this.props.classes;

    return (
      <div className={classes.container}>

        <TextField
          id="staffno"
          label={textData[0].label}
          type="number"
          className={classes.input}
          value={textData[0].value}
          onChange={this.handleRequestChange(0)}
          InputProps={{ placeholder: textData[0].placeholder }}
          marginForm
        />
        <TextField
          id="name"
          label={textData[1].label}
          type="text"
          className={classes.input}
          value={textData[1].value}
          onChange={this.handleRequestChange(1)}
          InputProps={{ placeholder: textData[1].placeholder }}
          marginForm
        />
        <TextField
          id="address"
          label={textData[2].label}
          type="text"
          className={classes.input}
          value={textData[2].value}
          onChange={this.handleRequestChange(2)}
          InputProps={{ placeholder: textData[2].placeholder }}
          marginForm
        />
        <TextField
          id="nowork"
          label={textData[3].label}
          type="text"
          className={classes.input}
          value={textData[3].value}
          onChange={this.handleRequestChange(3)}
          InputProps={{ placeholder: textData[3].placeholder }}
          marginForm
        />

      </div>
    );
  }
}

TextFields.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styleSheet)(TextFields);




